import {
  Button,
  EmailInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { SET_USER_DATA_EMAIL_TOKEN } from "../../services/actions/auth";
import { burgerApiUrl, passwordForgotApi } from "../../utils/Api";


import styles from "./stylesForm.module.css";

export default function ForgotPasswordPage() {
  const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const history = useHistory();

  const ChangeInput = (e) => {
    setEmail(e.target.value);
  };

  const SubmitForm = (e) => {
    e.preventDefault();
    passwordForgotApi(burgerApiUrl, email)
      .then((res) => {
        if (res && res.success) {
          dispatch({
            type: SET_USER_DATA_EMAIL_TOKEN,
          });
          history.replace({ pathname: "/reset-password" });
          setEmail("");
        }
      })
      .catch((err) => {
        console.log(` ${err}`);
      });
  };

  return (
    <form onSubmit={SubmitForm} className={styles.container}>
      <p className="mb-6 text text_type_main-small"> </p>
      <EmailInput
        onChange={ChangeInput}
        value={email}
        placeholder=" e-mail"
        extraClass="mb-6"
      />
      <Button size="medium" extraClass="mb-20">
        
      </Button>
      <p className="text text_type_main-default text_color_inactive">
         ?{" "}
        <Link to="/login" className={styles.link}>
          
        </Link>
      </p>
    </form>
  );
}
